import React,{useEffect,useState} from 'react'
import {useParams,useHistory,Link} from 'react-router-dom'
import axios from 'axios'

import People from './People'



function Home({input}) {

  const [characters,setCharacters] = useState([])
  const history = useHistory()
  const {page} = useParams()
  let currentPage = Number(page); 
  
  
  if (!currentPage){
     history.push(`/charactersPage/1`)
    }
  
    const axiosGetCharacters = async(page) => {
      const url = `https://rickandmortyapi.com/api/character/?page=${page}`;
      try{await axios.get(url).then((({data}) => setCharacters(data.results)));}
      catch(e){
        console.error(e);
      }
    };
    
  useEffect(()=>{
      axiosGetCharacters(currentPage);
  },
  [page]);
    

    return (
        <div className='page'>
           {characters.map((item,id)=>item.name.toLowerCase().includes(input.toLowerCase())
           ? <People key={id} {...item}/>
           : null
        )}
            <Link to={`/charactersPage/${currentPage - 1}`}>Prev</Link>
            {currentPage}
            <Link to={`/charactersPage/${currentPage + 1}`}>Next</Link>
        </div>
    )
}

export default Home
