import React from 'react'
import classNames from 'classnames'
import {Link} from 'react-router-dom'

function People({name,image,gender,id,status,species,location}) {

    const checkStatus = classNames({
        "status_alive":status === 'Alive',
        "status_dead":status === 'Dead',
        "status_unknown":status === 'unknown'
    })


    return (
        <div className='peopleCard__container'>
            <img src={image}/>
            <div className='peopleCard__container_info'>
                <Link to={`/personCart/${id}`}><p onClick={()=>console.log('PersoneClick')} className='peopleCard__container_info_name'>{name}</p></Link>
                <span><span className={checkStatus}></span>{status}</span>
                <p>{gender} - {species}</p>
                <p>{location.name}</p>
            </div>
        </div>
    )
}

export default People
