import React,{useEffect,useState} from 'react'
import {useHistory,useParams} from 'react-router-dom'
import axios from 'axios'
import classNames from 'classnames'

function PersonCart() {

    const [person,setPerson] = useState({})
    const history = useHistory()
    const {id} = useParams()
    console.log(person)

    const {name,gender,status,image,species,created} = person
    
    
    const axiosGetPerson = async (id) => {
        const url = `https://rickandmortyapi.com/api/character/${id}`;
        try{
            await axios.get(url).then((({data}) => setPerson(data)))
        }
        catch(e){console.error(e)};
        };

    
        useEffect(()=> axiosGetPerson(id)
      ,[id]);

     

      const checkStatus = classNames({
        "status_alive":status === 'Alive',
        "status_dead":status === 'Dead',
        "status_unknown":status === 'unknown'
    })

    return (
        <div className='personCart__container'>
            <div className="personCart__container-info">
                <div className="personCart__container-image">
                    <img src={image} alt="person" />
                </div>
                <div className="personCart__container-information">
                    <h1>{name}</h1>
                    <span><span className={checkStatus}></span>{status}</span>
                    <p>Create: {created}</p>
                    <p>Species: {species}</p>
                    <p>Gender: {gender}</p>
                </div>
                <button  className='' onClick={()=> history.goBack()}>GoBack</button>
            </div>
            
        </div>
    )
}

export default PersonCart
